body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: #f4f4f9;
}

.container {
  padding: 20px;
  min-height: 100vh;
}

.title {
  text-align: center;
  color: #333;
}

.recipe-card {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  padding: 20px;
  margin-bottom: 20px;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 20px;
  align-items: flex-start;
}

.recipe-image {
  width: 250px;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
}

.recipe-content {
  width: 500px;;
}

.recipe-content h2 {
  margin: 0 0 10px 0;
  color: #1e3a8a;
}

.recipe-meta {
  font-size: 0.9em;
  color: #555;
  margin-bottom: 10px;
}

.recipe-content ul,
.recipe-content ol {
  margin: 5px 0 15px 20px;
}

.tags {
  margin-top: 10px;
}

.tag {
  display: inline-block;
  background: #1e3a8a;
  color: #fff;
  padding: 3px 8px;
  border-radius: 5px;
  font-size: 0.8em;
  margin-right: 5px;
  margin-bottom: 5px;
}
